<template>
<div style="position:fixed;margin:0px;height:100%;width:100%;background-color: #c86dd7;
        background: -moz-linear-gradient(-45deg,  #3023ae 0%, #c86dd7 100%);
        background: -webkit-gradient(linear, left top, right bottom, color-stop(0%,#3023ae), color-stop(100%,#c86dd7));
        background: -webkit-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%) ;
        background: -o-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%);
        background: -ms-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%);
        background: linear-gradient(135deg,  #3023ae 0%,#c86dd7 100%);
        background-attachment: fixed;">
   <mu-appbar  title="成人确认" >
     
   </mu-appbar>
   <div class="center">
     <img src="https://ooo.0o0.ooo/2017/06/24/594e330a587a5.jpg" style="width:80px;">
     <p>警告：本站含有成人内容，未满18岁者请勿进入！</p>
     <p>WARNING:This Site Contains Adult Contents, No Entry For Less Than 18-Years-Old !<p>
     <p><strong>你是否已经年满十八岁</strong></p>

     <mu-raised-button   label="YES" @click="yes()" class="demo-raised-button" primary/>
     <mu-raised-button  label="NO" @click="no()" class="demo-raised-button" primary/>
     
   </div>
 
</div>
</template>
<script >
  export default {
    name:'login',
    data(){
      return {
        
      }
    },
    beforeRouteEnter(to,from,next){
      //localStorage.clear()
      if(localStorage.more18){
         next("/index")
      }
      else {
        next()
      }
    },
    methods: {

      yes()
      {   
          localStorage.more18=true;
          this.$router.push({path:'/index'})
      },
      no()
      {
          window.location="https://www.google.com"

      },
      goback()
      {
        this.$router.go(-1)
      }
     
   } 
  }
</script>
<style scoped>
  
   .flex{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
   }
   .center{
     margin: 30px;
     margin-top: 80px;
     text-align: center;
     color: white;
     
   
   }
   .desc-container{
    height:100%;
    overflow-y: scroll;
   }
   img {
    width: 100%;
   }
   .fixed {
    position:fixed;
    top: 0;

   }
 
</style>


